import React from 'react';
import PropTypes from 'prop-types';
import {DvpBar as RealComponent} from '../LazyLoader';

/**
 * Ant Design word cloud
 */
const DvpBar = (props) => {
    return (
        <React.Suspense fallback={null}>
            <RealComponent {...props} />
        </React.Suspense>
    );
};

DvpBar.defaultProps = {
    labelPosition: 'middle',
    direction: 'horizontal',
    legendPosition: 'right',
    isStack: true
};

DvpBar.propTypes = {
    /**
     * Data
     */
    data: PropTypes.array,

    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * Dict of colors
     */
    colors: PropTypes.object,

    /**
     * Position of label
     */
    labelPosition: PropTypes.oneOf(['left', 'middle', 'right']),

    /**
     * Padding
     */
    padding: PropTypes.arrayOf(PropTypes.number),

    /**
     * Sub info
     */
    subInfo: PropTypes.node,

    /**
     * X axis title
     */
    xAxisTitle: PropTypes.string,

    /**
     * Y axis title
     */
    yAxisTitle: PropTypes.string,

    /**
     * Whether to hide x axis
     */
    hideXaxis: PropTypes.bool,

    /**
     * Whether to hide y axis
     */
    hideYaxis: PropTypes.bool,

    /**
     * Whether to hide x axis title
     */
    hideXaxisTitle: PropTypes.bool,

    /**
     * Whether to hide y axis title
     */
    hideYaxisTitle: PropTypes.bool,

    /**
     * Whether to hide legend
     */
    hideLegend: PropTypes.bool,

    /**
     * Stack mode
     */
    isStack: PropTypes.bool,

    /**
     * 100% percent mode
     */
    isPercent: PropTypes.bool,

    /**
     * Legend position
     */
    legendPosition: PropTypes.oneOf(['top', 'top-left', 'top-right', 'left',
    'left-top', 'left-bottom', 'right', 'right-top', 'right-bottom','bottom', 'bottom-left', 'bottom-right']),

    /**
     * Chart title
     */
    chartTitle: PropTypes.string,

    /**
     * Style of chart tile
     */
    chartTitleStyle: PropTypes.object,

    /**
     * Direction of the bar
     */
    direction: PropTypes.oneOf(['horizontal', 'vertical']),

    /**
     * X axis
     */
    xField: PropTypes.string,

    /**
     * X axis
     */
    yField: PropTypes.string,

    /**
     * Field to show as label
     */
    labelField: PropTypes.string,

    /**
     * Group by field
     */
    groupbyField: PropTypes.string,

    /**
     * CSS classes to be added to the component
     */
    className: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),

    /**
     * Inline CSS style
     */
    style: PropTypes.object,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
};

export default DvpBar;

export const defaultProps = DvpBar.defaultProps;
export const propTypes = DvpBar.propTypes;
