import React from 'react';
import PropTypes from 'prop-types';
import { DvpWordGraph as RealComponent } from '../LazyLoader';

/**
 * Word network graph based on Echarts
 */
const DvpWordGraph = (props) => {
    return (
        <React.Suspense fallback={null}>
            <RealComponent {...props}/>
        </React.Suspense>
    );
};

DvpWordGraph.defaultProps = {
    'layoutType': 'none',
    'lineStyle': {
        color: 'source',
        curveness: 0.3,
    },
    draggable: true,
    queryURL: 'https://www.google.com/search?q=',
};

DvpWordGraph.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * Data
     */
    data: PropTypes.object,

    /**
     * CSS classes to be added to the component
     */
    className: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.object
    ]),

    /**
     * Layout type
     */
    layoutType: PropTypes.string,

    /**
     * Line style
     */
    lineStyle: PropTypes.object,

    /**
     * Force
     */
    force: PropTypes.object,

    /**
     * Draggable
     */
    draggable: PropTypes.bool,

    /**
     * Query url
     */
    queryURL: PropTypes.string,

    /**
     * Inline CSS style
     */
    style: PropTypes.object,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};

export default DvpWordGraph;

export const defaultProps = DvpWordGraph.defaultProps;
export const propTypes = DvpWordGraph.propTypes;
