import React from 'react';
import PropTypes from 'prop-types';
import { DvpWordCloud as RealComponent } from '../LazyLoader';

/**
 * Ant Design word cloud
 */
const DvpWordCloud = (props) => {
    return (
        <React.Suspense fallback={null}>
            <RealComponent {...props}/>
        </React.Suspense>
    );
};

DvpWordCloud.defaultProps = {
    queryURL: 'https://www.google.com/search?q=',
    fontSize: [24, 80]
};

DvpWordCloud.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * Data
     */
    data: PropTypes.array,
    

    /**
     * wordField
     */
    wordField: PropTypes.string,

    /**
     * colorField
     */
    colorField: PropTypes.string,

    /**
     * weightField
     */
    weightField: PropTypes.string,

    /**
     * Range of font size
     */
    fontSize: PropTypes.arrayOf(PropTypes.number),

    /**
     * Query url
     */
    queryURL: PropTypes.string,

    /**
     * CSS classes to be added to the component
     */
    className: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.object
    ]),

    /**
     * Inline CSS style
     */
    style: PropTypes.object,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};

export default DvpWordCloud;

export const defaultProps = DvpWordCloud.defaultProps;
export const propTypes = DvpWordCloud.propTypes;
