import React from 'react';
import PropTypes from 'prop-types';
import { DvpCharts as RealComponent } from '../LazyLoader';

/**
 * ExampleComponent is an example component.
 */
const DvpCharts = (props) => {
    return (
        <React.Suspense fallback={null}>
            <RealComponent {...props}/>
        </React.Suspense>
    );
};

DvpCharts.defaultProps = {};

DvpCharts.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * A label that will be printed when this component is rendered.
     */
    label: PropTypes.string.isRequired,

    /**
     * The value displayed in the input.
     */
    value: PropTypes.string,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};

export default DvpCharts;

export const defaultProps = DvpCharts.defaultProps;
export const propTypes = DvpCharts.propTypes;
