import React from 'react';
import PropTypes from 'prop-types';
import {DvpScatter as RealComponent} from '../LazyLoader';

/**
 * Ant Design word cloud
 */
const DvpScatter = (props) => {
    return (
        <React.Suspense fallback={null}>
            <RealComponent {...props} />
        </React.Suspense>
    );
};

DvpScatter.defaultProps = {
    legendPosition: 'right',
    legendDirection: 'vertical',
    size: 3
};

DvpScatter.propTypes = {
    /**
     * Data
     */
    data: PropTypes.array,

    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,


    /**
     * X axis title
     */
    xAxisTitle: PropTypes.string,

    /**
     * Y axis title
     */
    yAxisTitle: PropTypes.string,

    /**
     * Size field
     */
    sizeField: PropTypes.string,

    /**
     * Size or size range
     */
    size: PropTypes.oneOfType([PropTypes.number, PropTypes.array]),

    /**
     * Whether to hide x axis
     */
    hideXaxis: PropTypes.bool,

    /**
     * Whether to hide y axis
     */
    hideYaxis: PropTypes.bool,

    /**
     * Padding
     */
    padding: PropTypes.arrayOf(PropTypes.number),

    /**
     * Legend position
     */
    legendPosition: PropTypes.oneOf(['top', 'top-left', 'top-right', 'left',
    'left-top', 'left-bottom', 'right', 'right-top', 'right-bottom','bottom', 'bottom-left', 'bottom-right']),

    /**
     * Legend direction
     */
    legendDirection: PropTypes.oneOf(['horizontal', 'vertical']),

    /**
     * Dicts of regression lines
     */
    lineDicts: PropTypes.arrayOf(PropTypes.object),

    /**
     * Chart title
     */
    chartTitle: PropTypes.string,

    /**
     * Style of chart tile
     */
    chartTitleStyle: PropTypes.object,

    /**
     * X axis
     */
    xField: PropTypes.string,

    /**
     * X axis
     */
    yField: PropTypes.string,

    /**
     * Group by field
     */
    groupbyField: PropTypes.string,

    /**
     * CSS classes to be added to the component
     */
    className: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),

    /**
     * Inline CSS style
     */
    style: PropTypes.object,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
};

export default DvpScatter;

export const defaultProps = DvpScatter.defaultProps;
export const propTypes = DvpScatter.propTypes;
